import React, { Fragment } from 'react';
import { Link } from 'react-router-dom'
import './footer.scss';
import logo from '../../static/img/logo.svg';
import email from '../../static/img/email.svg';
import facebook from '../../static/img/facebook.svg';
import instagram from '../../static/img/instagram.svg';
import twitter from '../../static/img/twitter.svg';

class Footer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            anchor: "",
            mobile: false
        }
        this.handleAnchor = this.handleAnchor.bind(this);
    }
    handleAnchor(anchorId) {
        this.setState({
            anchor: anchorId
        })
    }
    componentDidUpdate() {
        let anchor = document.getElementById(this.state.anchor);
        if (anchor) {
            anchor.scrollIntoView();
        }
    }
    componentDidMount() {
        var clientWidth = document.documentElement.clientWidth || document.body.clientWidth;
        if (clientWidth < 800) {
            this.setState({
                mobile: true
            })
        }
        else {
            this.setState({
                mobile: false
            })
        }
    }
    render() {
        return (
            <Fragment>
                <div className="footer">
                    <div><img src={logo} alt="logo"></img></div>
                    <div className="description-mobile">
                        <p>bThere.ai</p>
                        <p>bThere.ai provides technology that allows robot development teams to remotely monitor and control robots, autonomous vehicles, and IoT devices from anywhere in the world, with low-latency and high-reliability.</p>
                    </div>
                    <div className="nav-item">
                        <p onClick={() => this.handleAnchor("products")}><Link to="/products">Products</Link></p>
                        <ul>
                            {this.state.mobile ?
                                <li onClick={() => this.handleAnchor("team")}><Link to="/team">Team</Link></li> :
                                <li onClick={() => this.handleAnchor("team")}><Link to="/">Team</Link></li>
                            }
                            {this.state.mobile ?
                                <li onClick={() => this.handleAnchor("faq")}><Link to="/faq">FAQ</Link></li> :
                                <li onClick={() => this.handleAnchor("faq")}><Link to="/products">FAQ</Link></li>
                            }
                        </ul>
                    </div>
                    <div>
                        {this.state.mobile ?
                            <p onClick={() => this.handleAnchor("contact-us")}><Link to="/contact">Contact</Link></p> :
                            <p onClick={() => this.handleAnchor("contact-us")}><Link to="/">Contact</Link></p>
                        }
                        <ul>
                            <li>hello@bthere.ai</li>
                            <li>
                                <a href="mailto:hello@bthere.ai" target="_blank" rel="noopener noreferrer"><img src={email} alt="email" /></a>
                                <a href="https://www.facebook.com/bthereai" target="_blank" rel="noopener noreferrer"><img src={facebook} alt="facebook" /></a>
                                <a href="https://www.linkedin.com/company/bthere-ai/" target="_blank" rel="noopener noreferrer"><img src={instagram} alt="instagram" /></a>
                                <a href="https://www.instagram.com/bthere.ai/" target="_blank" rel="noopener noreferrer"><img src={twitter} alt="twitter" /></a>
                            </li>
                        </ul>
                    </div>
                </div>
                <div> <p className="copyright">© 2020 bThere.ai. All rights reserved.</p></div>
            </Fragment>
        )
    }
}

export default Footer;