import React from 'react';
import { Route, Switch } from "react-router-dom";
import Navigator from './Header/';
import HowItWorks from './HowItWorks/';
import Footer from './Footer/';
import FeatureList from "./Features/";
import Demo from "./Useage/";
import ServiceList from "./ServiceList/";
import TeamMobile from "./MobileComponent/Team";
import ContactMobile from "./MobileComponent/Contact";
import QuestionListMobile from "./MobileComponent/Faq";


import ContactUs from './ContactsUS/';
import Products from "./Products/";
import Team from './TeamSlices';


function Bthere(props) {
    return (
        <div className="web-container" id="web-container">
            <header className="clearfix">
                <Navigator />
            </header>
            <Switch>
                <Route exact path="/">
                    <section id="home">
                        <HowItWorks />
                    </section>
                    <section>
                        <FeatureList />
                    </section>
                    <section>
                        <Demo />
                    </section>
                    <section>
                        <ServiceList />
                    </section>
                    <section id="team">
                        <Team />
                    </section>
                    <section id="contact-us">
                        <ContactUs />
                    </section>
                </Route>
                <Route exact path="/products">
                    <section id="products">
                        <Products />
                    </section>
                </Route>
                <Route exact path="/team">
                    <section>
                        <TeamMobile />
                    </section>
                </Route>
                <Route exact path="/contact">
                    <section id="mb-contact-us">
                        <ContactMobile />
                    </section>
                </Route>
                <Route exact path="/faq">
                    <section id="faq">
                        <QuestionListMobile />
                    </section>
                </Route>
            </Switch>
            <footer id="footer">
                <Footer />
            </footer>
        </div >
    );
}

export default Bthere;
