import React, { Fragment, useState, useEffect } from "react";
import howitworks from "../../static/img/howitworks.svg";
import player from "../../static/img/howitworks-player.svg";
import ReactPlayer from 'react-player/youtube';
import "./howitworks.scss";
const HowItWorks = () => {
  const [playVideo, setPlayVideo] = useState(false);
  const handlePlayVideo = () => {
    setPlayVideo(true);
  }

  useEffect(() => {
    document.getElementById("web-container").addEventListener("click", function () {
      setPlayVideo(false);
    })
  },[playVideo])
  return (
    <Fragment>
      <div className="how-it-works clearfix flexbox">
        <div>
          <img src={howitworks} alt="how it works" />
        </div>
        <div>
          <p>Monitor and Control Robots, from anywhere in the world</p>
          <p>
            bThere.ai provides technology that allows you to remotely monitor
            and control robots, autonomous vehicles, and IoT devices from
            anywhere in the world, with low-latency and high-reliability.
          </p>
          <div className="clearfix">
            <RequstDemo></RequstDemo>
            <p className="youtube"
              rel="noopener noreferrer"
              onClick={() => handlePlayVideo()}
            >
              <img src={player} alt="paly a video about bThere.ai" />
              How it Works
            </p>
          </div>
        </div>
        {playVideo && <div className="video-container">
          <ReactPlayer
            url="https://www.youtube.com/watch?v=UWPdsFnL0PU"
            width='100%'
            height='100%'
            config={{
              youtube: {
                playerVars: { showinfo: 1 }
              }
            }}

          />
        </div>}
      </div>
    </Fragment>
  );
};

const RequstDemo = () => {
  return <button><a href=" https://console.bthere.ai/signup">Sign Up</a></button>;
};

export default HowItWorks;
