import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "./servicelist.scss";
import service1 from "../../static/img/service-icon1.svg";
import service2 from "../../static/img/service-icon2.svg";
import service3 from "../../static/img/service-icon3.svg";
import service4 from "../../static/img/service-icon4.svg";
import service5 from "../../static/img/service-icon5.svg";
import service6 from "../../static/img/service-icon6.svg";
import service7 from "../../static/img/service-icon7.svg";





const Card = (props) => {
    return (
        <div className={"card flexbox   card-" + props.no}>
            <div className="icon-container">
                <img src={props.icon} alt=""></img>
            </div>
            <div className="card-name"><p>{props.name}</p></div>
            {props.description && (
                <div className="card-info">{props.description}</div>
            )}
            {props.btn && (<div className="card-btn">{props.mobile?<Link to="/contact">Contact Us</Link>:<a href="#contact-us">Contact Us</a>}</div>)}
        </div>
    );
};

function CardList() {
    const [mobile, setMobile] = useState(false)
    const services = [
        {
            src: service1,
            name: "Agriculture",
        },
        {
            src: service2,
            name: "Retail",
        },
        {
            src: service3,
            name: "Autonomous Vehicles",
        },
        {
            src: service4,
            name: "Maker",
        },
        {
            src: service5,
            name: "IoT",
        },
        {
            src: service6,
            name: "Academia/Research",
        },
    ];
    useEffect(() => {
        var clientWidth = document.documentElement.clientWidth || document.body.clientWidth;
        if (clientWidth < 800) {
            setMobile(true)
        }
        else {
            setMobile(false)
        }
    },[mobile])

    return (
        <div className="service-container">
            <div className="service-title">
                <h1>Applications We Support</h1>
                <p>Our solutions are versatile and robust to address robotics challenges across industries (no matter what your industry is!).</p>
            </div>
            <div className="service-list">
                {services.map((service, index) => {
                    return (
                        <Card
                            key={index}
                            icon={service.src}
                            name={service.name}
                            btn={false}
                            no={index}
                            mobile={mobile}
                        />
                    );
                })}
                <div className="list-r">
                    <Card
                        icon={service7}
                        name="AND MORE!"
                        description="Have something else in mind? Connect with us directly to explore how we can help you and your team."
                        btn={true}
                        mobile={mobile}
                    />
                </div>
            </div>
        </div>
    );
}

export default CardList;
