import React, { useState, useEffect } from "react";
import figure1 from "../../../static/img/figure-1.svg";
import figure2 from "../../../static/img/figure-2.svg";
import figure3 from "../../../static/img/figure-3.png";
import { Link } from "react-router-dom";
import './UserFlow.scss';

const UserflowCard = (props) => {
  return (
    <div className="uf-card-container card">
      <div className="uf-figure" style={{ backgroundImage: `url(${props.figure})` }}></div>
      <div className="uf-title">{props.title}</div>
      <div className="description">{props.description}</div>
    </div>
  );
};

const GetStart = (props) => {
  const [anchor, setAnchor] = useState("");
  const [mobile, setMobile] = useState(false);

  const handleContactClick = (anchorId) => {
    window.contactFromProducts = true;
    setAnchor(anchorId)
  }
  useEffect(() => {
    let anchorObj = document.getElementById(anchor);
    if (anchorObj) {
      anchorObj.scrollIntoView();
    }
    var clientWidth = document.documentElement.clientWidth || document.body.clientWidth;
    if (clientWidth < 800) {
      setMobile(true);
    }
    else {
      setMobile(false);
    }
  }, [anchor])
  return (
    <div className="get-start-container card flexbox">
      <div className="get-start-txt">
        <div className="txt-title">Join our beta...</div>
        <div className="description">
          Being part of the beta is at no cost to you! Get early access to bThere’s advanced solutions.
          Limited space available.
        </div>
      </div>
      <div className="get-start-btn">
        {mobile?
          <div className="btn-body" onClick={() => handleContactClick("contact-us")}><Link to="/contact">Contact Us</Link></div>:
          <div className="btn-body" onClick={() => handleContactClick("contact-us")}><Link to="/">Contact Us</Link></div>
        }
      </div>
    </div>
  );
};

function UserFlow() {
  return (
    <div className="user-flow-container flexbox">
      <div className="user-flow-top flexbox">
        <UserflowCard
          figure={figure1}
          title="Sign up"
          description="Create an account and install the robot agent."
        ></UserflowCard>
        <UserflowCard
          figure={figure2}
          title="Connect"
          description=" Log into our secured console and connect to your robot."
        ></UserflowCard>
        <UserflowCard
          figure={figure3}
          title="Remote Control"
          description="Stream live video telemetry and do your administrative tasks!"
        ></UserflowCard>
      </div>
      <div className="user-flow-bottom">
        <GetStart></GetStart>
      </div>
    </div>
  );
}

export default UserFlow;