import React from 'react';
import Swiper from 'react-id-swiper';
import member1 from '../../static/img/member-1.png';
import member2 from '../../static/img/member-2.png';
import member3 from '../../static/img/member-3.png';
import linkedin from '../../static/img/Linkedin2.png';
import "./team.scss";

const Team = () => {
  const params = {
    slidesPerView: 1,
    spaceBetween: 30,
    loop: true,
    autoplay: {
      delay: 5000,
      disableOnInteraction: false
    },
    pagination: {
      el: '.swiper-pagination',
      clickable: true
    },
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev'
    }
  }
  const members = [
    {
      id: 1,
      figure: member1,
      title: "Co-founder",
      name: "Stuart Marshall",
      words: '" We\'re bringing our internet services expertise to help you and your team to build solutions faster. "',
      linkdinUrl: "https://www.linkedin.com/in/thestuartmarshall/",
      intro: "Stuart has 26 years in software development/management. He worked at managing teams in AWS, Microsoft, and the computer game industry. Stuart is an expert in SaaS and high-scale distributed systems."
    },
    {
      id: 2,
      figure: member2,
      title: "Co-founder",
      name: "Matt Wenger",
      words: '" Making you a successful robot or IOT developer is our passion." ',
      linkdinUrl: "https://www.linkedin.com/in/wengermatt/",
      intro: "Matt is an expert in security, distributed, and embedded systems and has 24 years of experience in software / hardware development / management. He has worked in software, hardware, and management teams at Amazon, Oracle, and early stage companies. "
    },
    {
      id: 3,
      figure: member3,
      title: "Growth & Design",
      name: "Zaris Shen",
      words: '" We\'d love to hear what you’re working on and how we can help you be successful. "',
      linkdinUrl: "https://www.linkedin.com/in/zarisshen/",
      intro: "Zaris has worked at prominent VC companies and VC-funded SaaS startups in sales and marketing roles. She is an expert in UX and graphical design."
    }
  ]
  return (
    <div>
      <div className="team-title" >
        <h1>Our Team</h1>
      </div>
      <div className="slider-container">
        <Swiper {...params}>
          {
            members.map((member, index) => {
              return (
                <div key={index} >
                  <div className="mb-words">{member.words}</div>
                  <div className="flexbox">
                    <div className="mb-image">
                      <img src={member.figure} alt="profile pic" />
                    </div>
                    <div className="mb-info">
                      <div className="mb-title">{member.title}</div>
                      <div className="mb-name">{member.name} <a href={member.linkdinUrl} rel="noopener noreferrer" target="_blank"><img src={linkedin} alt="" /></a></div>
                      <div className="mb-intro">{member.intro}</div>
                    </div>
                  </div>
                </div>
              )
            })
          }
        </Swiper>
      </div>
    </div>
  )
};

export default Team;
