import React, { useState, useEffect } from "react";
import './contactus.scss';
import seattle from "../../../static/img/seattle.svg";
import close from '../../../static/img/close.svg';
import mail from '../../../static/img/mail.svg';

function FormCard() {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("Get in touch with our team!  We’d love to hear about your project.");
    const [closeFormResponse, setFormResponse] = useState(false);

    function handleSubmit(e) {
        e.preventDefault();
    }
    function handleFormResponse() {
        const reg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
        const formData = {};
        if (name && email && message) {
            if (reg.test(email)) {
                setFormResponse(!closeFormResponse);
                formData['name'] = name;
                formData['email'] = email;
                formData['message'] = message;
                console.log(formData)
            }
        }
    }
    useEffect(() => {
        if (window.contactFromProducts) {
            window.contactFromProducts = false;
            let contactAnchor = document.getElementById("mb-contact-us");
            if (contactAnchor) {
                contactAnchor.scrollIntoView();
                setMessage("Hi, I’m interested in joining your beta and trying the bThere tele-operations solutions.  Our project is…")
            }
        }
    }, [closeFormResponse])
    return (
        <div className="card form-container flexbox">
            <div className="form-title">Contact Us</div>
            <form onSubmit={handleSubmit}>
                <div className="ip-group">
                    <label>Full Name</label>
                    <input name="name" type="text" placeholder="your name" required autoComplete="false"
                        onChange={e => setName(e.target.value)}
                    />
                </div>
                <div className="ip-group">
                    <label>Email</label>
                    <input name="email" type="email" placeholder="your@email.com" required autoComplete="false"
                        onChange={e => setEmail(e.target.value)}
                    />
                </div>
                <div className="ip-group">
                    <label>Tell us about you needs</label>
                    <textarea name="message" placeholder={message} required autoComplete="false"
                        onChange={e => setMessage(e.target.value)}
                    />
                </div>
                <p className="form-note">*Your information will only be used by bThere.ai to get in touch you about your needs. </p>
                {/* <div class="g-recaptcha" data-sitekey="6Lc9xKgZAAAAAGiRqoJpAWtcW68VKWwNEHvlLYAG"></div> */}
                <input type="submit" onClick={handleFormResponse} value="SEND"/>
            </form>
            {closeFormResponse && <FormResonse handleFormResponse={handleFormResponse} value="SEND"/>}
        </div>
    );
}
function ContactMobile() {
    return (
        <section className="mb-contact-container" id="contact">
            <section className="contact-form">
                <FormCard></FormCard>
            </section>
            <section className="contact-figure">
                <div className="contact-title">
                    <h1>Let's get in touch!</h1>
                    <p>Connect with us through email: <a href="mailto:hello@bthere.ai">hello@bthere.ai</a></p>
                </div>
                <div className="figure-img">
                    <img src={seattle} alt="icon" />
                </div>
            </section>
        </section>
    )
}

function FormResonse(props) {
    return (
        <div className="form-response">
            <div>
                <img src={close} alt="close the form" className="close" onClick={props.handleFormResponse} />
            </div>
            <div className="description">
                <p>Thank you for contacting us! </p>
                <p>We will response to your needs within 2 business days.
                Please also fee free to email hello@bThere.ai directly.
                </p>
                <img src={mail} alt="mail icon" />
            </div>
        </div>
    )
}

export default ContactMobile;