import React from 'react';
import './Overview.scss'
import prototype from '../../../static/img/prototype.svg';

function Overview() {
    return <div id="products">
        <div className="overview-description">
            <p>Overview of Service</p>
            <p>All of our services work whether your robots are on WiFi or cell. It works with robots 2 miles away or on the other side of the planet. </p></div>
        <div className="prototype-container flexbox">
            <img src={prototype} alt=""></img>
        </div>
    </div>

}

export default Overview;