import React from 'react';
import logo from '../../static/img/logo.svg';
import nav_icon_close from '../../static/img/nav-icon-close.svg';
import nav_icon_open from '../../static/img/nav-icon-open.svg';


import './navigator.scss';
import { Link } from "react-router-dom";

class Navigotor extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            anchor: "",
            mobile: false,
            navBar: false
        }
        this.handleAnchor = this.handleAnchor.bind(this);
    }
    handleAnchor(anchorId) {
        this.setState({
            anchor: anchorId
        })
    }

    handleNavBar() {
        this.setState({
            navBar: !this.state.navBar
        })

    }
    componentDidUpdate() {
        let anchor = document.getElementById(this.state.anchor);
        let navBarMobile = document.getElementById('mobile-nav');


        if (anchor) {
            anchor.scrollIntoView();
        }
        if (!this.state.navBar) {
            navBarMobile.style.visibility = "hidden";
        }
        else {
            navBarMobile.style.visibility = "visible"
        }
    }
    render() {
        return (
            <div className="flexbox header-container" >
                <div className="logo" onClick={() => this.handleAnchor("home")}>
                    <Link to="/"><img src={logo} alt="logo" /></Link>
                    <div><Link to="/">bThere.ai</Link></div>
                </div>
                <img src={nav_icon_open} alt="open nav bar" className="nav-open"
                    onClick={() => this.handleNavBar()} />
                <nav id="nav" onClick={() => this.handleNavBar()}>
                    <div className="nav-icon">
                        <img src={nav_icon_close} alt="close nav bar"
                            onClick={() => this.handleNavBar()} />
                    </div>
                    <ul className="web-nav" onClick={() => this.handleNavBar()}>
                        <li onClick={() => this.handleAnchor("home")}><Link to="/">Home</Link></li>
                        <li onClick={() => this.handleAnchor("products")}><Link to="/products">Products</Link></li>
                        <li onClick={() => this.handleAnchor("team")}> <Link to="/">Team</Link></li>
                        <li onClick={() => this.handleAnchor("faq")}> <Link to="/products">FAQ</Link></li>
                        <li onClick={() => this.handleAnchor("contact-us")}> <Link to="/">Contact Us</Link></li>
                        <li><a href="https://console.bthere.ai">Log in</a></li>
                    </ul>
                    <ul className="mobile-nav" id="mobile-nav" onClick={() => this.handleNavBar()}>
                        <li><img src={nav_icon_close} alt="close nav bar"
                            onClick={() => this.handleNavBar()} /></li>
                        <li onClick={() => this.handleAnchor("home")}><Link to="/">Home</Link></li>
                        <li onClick={() => this.handleAnchor("products")}><Link to="/products">Products</Link></li>
                        <li onClick={() => this.handleAnchor("team")}><Link to="/team">Team</Link></li>
                        <li onClick={() => this.handleAnchor("faq")}><Link to="/faq">FAQ</Link></li>
                        <li><Link to="/contact">Contact Us</Link></li>
                    </ul>
                    <a className="sign-up" href="https://console.bthere.ai/signup">Sign Up</a>
                </nav>
            </div>
        )
    }
}

export default Navigotor;