import React, { useEffect, useState } from 'react';
import Overview from './Overview/Overview';
import FeatureList from './FeatureList/FeatureList';
import UserFlow from './UserFlow/UserFlow';
import QuestionList from './QuestionList/QuestionList';
import './index.scss'

function Home() {
    const [title, setTitle] = useState(true)
    useEffect(() => {
        if (window.routerToProductsTop) {
            window.routerToProductsTop = false;
            let contactAnchor = document.getElementById("overview");
            if (contactAnchor) {
                contactAnchor.scrollIntoView();
            }
        }
        var clientWidth = document.documentElement.clientWidth || document.body.clientWidth;
        if (clientWidth < 800) {
            setTitle(true);
        }
        else {
            setTitle(false);
        }
    }, [title])
    return (
        <div className="home-page"  id="overview">
            <section className="overview">
                <Overview></Overview>
                <FeatureList></FeatureList>
            </section>

            <section className="getting-start">
                <h1 className="section-header">
                    {title ? "Get Started" : "Getting Started is Easier than You Think!"}
                </h1>
                <UserFlow></UserFlow>
            </section>

            {!title &&
                <section className="FAQ" id="faq">
                    <h1 className="section-header">Frequently Asked Questions</h1>
                    <QuestionList></QuestionList>
                </section>
            }
        </div>
    )
}


export default Home;