import React from 'react';
import feature_1 from '../../static/img/problem-icon1.svg';
import feature_2 from '../../static/img/problem-icon2.svg';
import feature_3 from '../../static/img/problem-icon3.svg';
import rocket from '../../static/img/rocket.svg';
import './features.scss'

const Feature = (props) => {
    return (
        <div className="homepage-feature flexbox">
            <div className="homepage-ft-icon"><img src={props.icon} alt="feature icon"/></div>
            <div className="homepage-ft-txt">
                <div className="txt-title">{props.title}</div>
                <div className="txt-description">{props.description}</div>
            </div>
        </div>
    )
}

function FeatureList() {
    const features = [
        {
            title: "Gets you to market faster",
            src: feature_1,
            description: "Focus on what you do best, building robots or IOT devices.  Let bThere help you stay connected to your devices."
        },
        {
            title: "Saves your developer time and money",
            src: feature_2,
            description: "No need to build it yourself.  Get access to your devices and data today."
        },
        {
            title: "Is more secure and is feature rich",
            src: feature_3,
            description: "End-to-end encrypted communications.  Control who can access your robot."
        }
    ]

    return (
        <div className="homepage-feature-list">
            {
                features.map((feature, index) => {
                    return <Feature key={index} title={feature.title} description={feature.description} icon={feature.src}></Feature>
                })
            }
        </div>
    )
}
function FeatureSection() {
    return (
        <section className="feature-list-container flexbox">
            <div className="list-content">
                <div className="section-title">
                    <h1>What bThere does for you?</h1>
                    <p>We solve a common set of problems for thousands of robot development teams.</p>
                </div>
                <FeatureList></FeatureList>
            </div>
            <div className="list-background">
                <img src={rocket} alt=""/>
            </div>
        </section>
    )
}

export default FeatureSection;