import React, { useState } from "react";
import { Link } from "react-router-dom";
import demo1 from '../../static/img/performance-icon1.svg';
import demo2 from '../../static/img/performance-icon2.svg';
import demo3 from '../../static/img/performance-icon3.svg';
import demo4 from '../../static/img/performance-icon4.svg';
import keyword_1 from '../../static/img/performance-icon5.svg';
import keyword_2 from '../../static/img/performance-icon6.svg';
import keyword_3 from '../../static/img/performance-icon7.svg';
import keyword_4 from '../../static/img/performance-icon8.svg';
import keyword_5 from '../../static/img/performance-icon9.svg';


import "./useage.scss";


const FeatureKeyWords = ({ keywords }) => {
  return (
    <div className="keywords-container flexbox">
      {
        keywords.map((keyword, index) => {
          return <div className="keyword-item flexbox" key={index}>
            <img src={keyword.icon} alt="keywords"></img>
            <div className="keyword-txt">{keyword.text}</div>
          </div>
        })
      }
    </div>
  )
}

function DemoSection() {
  const [keywords] = useState([
    {
      icon: keyword_1,
      text: 'Easy'
    },
    {
      icon: keyword_2,
      text: 'Scalable'
    },
    {
      icon: keyword_3,
      text: 'Secure'
    },
    {
      icon: keyword_4,
      text: 'Low Latency'
    },
    {
      icon: keyword_5,
      text: 'Reliable'
    }
  ])

  return (
    <section className="section-container">
      <div className="section-title">
        <h1>bThere’s Features</h1>
        <p>It’s easy, scalable, secure, reliable, and low latency.</p>
      </div>
      <Demo />
      <FeatureKeyWords keywords={keywords} />
    </section>
  )
}



const Tab = (props) => {
  return (
    <div className="tab-btn-container flexbox" onMouseOver={props.onMouseOver}>
      <div className="tab-btn-icon">
        <img src={props.icon} alt="demo icon" />
      </div>
      <div className="tab-btn-txt">
        <div className="title">{props.title}</div>
        <div className="description">{props.description}</div>
      </div>
    </div>
  );
};
function Demo(props) {
  const demoTabs = [
    {
      id: 1,
      src: demo1,
      title: "Remote Control",
      description: "Driving to complex operations",
    },
    {
      id: 2,
      src: demo2,
      title: "Multi-media Connectivity",
      description: "From single unidirectional video stream to full muti-media communications",
    },
    {
      id: 3,
      src: demo3,
      title: "Administrative Access ",
      description: "Direct secure root access to the operating systems",
    },
    {
      id: 4,
      src: demo4,
      title: "Cloud-based Telemetry",
      description: "Monitor live and historical metrics.  Get notified immediately is something goes wrong.",
    },
  ];
  const handleRouterToProducts = () => {
    window.routerToProductsTop = true;
  }
  return (
    <div className="demo-container flexbox">
      <div className="demo-tabs flexbox">
        {demoTabs.map((tab, index) => {
          return (
            <Tab
              id={index}
              icon={tab.src}
              title={tab.title}
              description={tab.description}
              key={index}
            />
          );
        })}
        <div className="button demo-btn" onClick={handleRouterToProducts}><Link to="/products">Learn More</Link></div>
      </div>
      <div className="demo-view">
        <div className="demo"></div>
      </div>
    </div>
  );
}

export default DemoSection;
